
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

export default {
  name:'card-order',
  data:function(){
    return {
      collapse_footer:false
    }
  },
  components:{
    Flexbox,
    FlexboxItem,
    FormPreview

  },
  methods:{
    collapse_change:function(){
      console.log("change");
      this.collapse_footer=!this.collapse_footer;
    }
  },
  props:{
    data:{
      type:Object
    }
  }
}



