import { mapState, mapActions } from 'vuex';

export default({
    // 映射 vuex 上面的属性
    computed: mapState({
        recommend: state => state.news.recommend
    }),
    created() {
        // 获取推荐列表
        this.GETRECOMMEND();
    },
    methods: {
        // 映射 vuex 对象上的方法
        ...mapActions([
            'GETRECOMMEND'
        ])
    }
});




