
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
  name: 'app',
  data: function() {
    return {
      popup_login: false,
      page_name: '',
      search_input: '',
      phone: '',
      pwd: '',
      // title:'用户注册',
      header_conf: {
        title: '',
        is_show: true,
        show_back: false
      },
      navbar: {
        data: [{
          iconfont: '&#xe6b8;',
          title: '商城',
          url: '/home'
        }, {
          iconfont: '&#xe616;',
          title: '分类',
          url: '/category'
        },
        {
          iconfont: '&#xe694;',
          title: '用户中心',
          url: '/user'
        },
        // {
        //   iconfont: '&#xe67b;',
        //   title: '购物车',
        //   url: '/cart'
        // }, {
        //   iconfont: '&#xe657;',
        //   title: '活动广场',
        //   url: '/activity'
        // }
      ],
        conf: {}
      }
    }
  },
  methods: {
    fetch_data: function() {
      this.popup_login = false;
      this.page_name = this.$route.name;
      this.xheader_handler(this.$route);
      //处理 navbar 的 active

    },
    popup: function() {
      this.popup_login = !this.popup_login;
    },
    xheader_handler: function(route) {
      var header_conf = this.handler_xheader(route.name);
      this.header_conf = header_conf;
    }
  },
  created: function() {
    //调用header处理
    this.page_name = this.$route.name;
    var header_conf = this.handler_xheader(this.$route.name);
    //console.log(header_conf);
    this.header_conf = header_conf;
  },
  watch: {
    $route: 'fetch_data'
  },
  components: {
    XHeader,
    Tabbar,
    TabbarItem,
    Search,
    Popup,
    Group,
    XInput,
    XButton
  }
}



